.experience{padding:80px 0;background:#fff}.experience__container{max-width:1200px;margin:0 auto;padding:0 24px}.experience__timeline{position:relative;margin-top:40px;padding:0 16px}.experience__timeline::before{content:"";position:absolute;left:20px;top:0;height:100%;width:3px;background:linear-gradient(to bottom, #0984e3, #00b894)}@media screen and (min-width: 768px){.experience__timeline::before{left:50%;transform:translateX(-50%)}}.experience__item{position:relative;margin-bottom:40px;width:100%;padding-left:48px}@media screen and (min-width: 768px){.experience__item{width:50%;padding-left:0}.experience__item:nth-child(odd){padding-right:48px;text-align:right;margin-left:0}.experience__item:nth-child(odd) .experience__header::after{left:auto;right:-42px}.experience__item:nth-child(even){margin-left:50%;padding-left:48px}.experience__item:nth-child(even) .experience__header::after{left:-42px}}.experience__item .experience__header{position:relative}.experience__item .experience__header::after{content:"";position:absolute;left:-42px;top:15px;width:16px;height:16px;border-radius:50%;background:#0984e3;border:4px solid #fff;box-shadow:0 0 0 3px rgba(9,132,227,.3);z-index:1}@media screen and (min-width: 768px){.experience__item .experience__header::after{top:50%;transform:translateY(-50%)}}.experience__header{background:hsla(0,0%,100%,.1);backdrop-filter:blur(10px);border:1px solid hsla(0,0%,100%,.2);padding:24px;border-radius:15px;transition:all .3s ease;position:relative}.experience__header:hover{transform:translateY(-5px);box-shadow:0 10px 20px rgba(0,0,0,.1)}.experience__role{margin-bottom:16px}.experience__role h3{color:#2d3436;font-size:1.3rem;margin-bottom:8px;font-weight:600}.experience__role .company{display:block;color:#0984e3;font-weight:500;font-size:1.1rem}.experience__meta{display:flex;flex-direction:row;justify-content:flex-start;align-items:center;gap:16px;color:#636e72;font-size:.9rem}@media screen and (min-width: 768px){.experience__meta{justify-content:flex-end}}.experience__meta .period{background:rgba(9,132,227,.1);padding:8px 16px;border-radius:20px;color:#0984e3}.experience__meta .location{display:flex;align-items:center;gap:8px}.experience__meta .location::before{content:"üìç"}@media screen and (max-width: 768px){.experience__timeline{padding:0 8px}.experience__timeline::before{left:20px}.experience__item{padding-left:40px;width:100%;margin-left:0 !important}.experience__item .experience__header::after{left:-42px !important;top:15px !important;transform:none !important}.experience__header{padding:16px}.experience__meta{flex-direction:column;align-items:flex-start;gap:8px}}